<template>
  <div class="now">
      <div v-for="data in datalist" :key="data.filmId" class="nowgroup" @click="$router.push('/detils/'+data.filmId)">
          <div class="nowlogo">
          <img :src="data.poster" alt="">
        </div>
        <div class="nowtext">
          <h3>{{data.name}}</h3>
          <p>观众评分：{{data.grade}}</p>
          <p class="actors">主演：{{data.actors | filterName}}</p>
          <p>{{data.nation}} | {{data.runtime}}分钟</p>
        </div>
        <div class="buy">购票</div>
      </div>
  </div>
</template>

<script>
import {instance} from '@/utils/http'
import {mapState} from 'vuex'
export default {
  data(){
    return{
      datalist:null
    }
  },
  computed:{
    ...mapState("city",["cityId"])
  },
  created(){
    instance(`/gateway?cityId=${this.cityId}&pageNum=1&pageSize=10&type=1&k=6557021`,{
      headers:{
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res=>{
      this.datalist = res.data.data.films
    })
  }
}

</script>
<style lang="scss" scoped>
.now{
  width: 100%;
  height: 124px;
  padding-left: 15px;
  .nowgroup{
    padding: 15px 15px 15px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .buy{
      width: .5rem;
      height: .25rem;
      border: 1px solid orange;
      text-align: center;
      line-height: .25rem;
      color: orange;
    }
    p{
      line-height: 24px;
    }
    .actors{
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 2.1rem;
    }
  .nowlogo{
    width: 66px;
    height: 90px;
    margin-right: .1rem;
    img{
      width: 100%;
      height: 100%;
    }
  }
  }
}
</style>